import React, { useState } from "react";
import { useDropzone } from "react-dropzone"; //  ( https://github.com/react-dropzone/react-dropzone )
import imageCompression from "browser-image-compression"; //  ( https://github.com/Donaldcwl/browser-image-compression )
import JSZip from "jszip"; // zip ( https://github.com/Stuk/jszip )
import { saveAs } from "file-saver"; //  (https://github.com/eligrey/FileSaver.js)
import "./styles/App.scss";

export const App = () => {
  const [files, setFiles] = useState<File[]>([]);

  // 
  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  // 
  const [errorMaxOveressage, setErrorMaxOveressage] = useState<string | null>(
    null
  );

  // 
  const MAX_FILES = 20;

  // 
  const compressOption = {
    initialQuality: 0.6,
    preserveExif: false,
  };

  // 
  const allowedFileTypes = ["image/png", "image/jpeg", "image/webp"];

  // 
  const handleDrop = async (acceptedFiles: File[]) => {
    setErrorMessage(null);

    // 
    const invalidFiles = acceptedFiles.filter(
      (file) => !allowedFileTypes.includes(file.type)
    );

    // 
    if (invalidFiles.length > 0) {
      const invalidFileNames = invalidFiles.map((file) => file.name).join(", ");
      setErrorMessage(
        ` : ${invalidFileNames}`
      );
      // 
      if (acceptedFiles.length > MAX_FILES) {
        setErrorMaxOveressage(
          `${MAX_FILES}`
        );
      }
      return;
    }

    // 
    if (acceptedFiles.length > MAX_FILES) {
      setErrorMaxOveressage(
        `${MAX_FILES}`
      );
      return;
    } else {
      const compressedFiles = await Promise.all(
        acceptedFiles.map(async (file) => {
          const compressed = await imageCompression(file, compressOption);
          return compressed;
        })
      );

      setErrorMaxOveressage(null);

      setFiles(compressedFiles);
    }
  };

  // 
  const handleDownload = () => {
    const zip = new JSZip();
    files.forEach((file, index) => {
      zip.file(`${file.name}`, file);
    });
    zip.generateAsync({ type: "blob" }).then((blob) => {
      saveAs(blob, "compressed_images.zip");
    });
  };

  // 
  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop: handleDrop,
  });

  return (
    <>
      <div className="file_input">
        <h1 className="c-heading c-heading-large"></h1>
        <p>
          {MAX_FILES}
          PNGJPGWEBP
        </p>
        <div {...getRootProps()}>
          <input {...getInputProps()} />
          {isDragActive ? (
            <div className="file_input--dragable">
               ...
            </div>
          ) : (
            <div className="file_input--dragable">
              
            </div>
          )}
        </div>
        {errorMessage && <p className="c-error_text">{errorMessage}</p>}
        {errorMaxOveressage && (
          <p className="c-error_text">{errorMaxOveressage}</p>
        )}
      </div>
      {files.length > 0 && !errorMessage && !errorMaxOveressage && (
        <div className="file_output">
          <h2 className="c-heading c-heading-medium"></h2>
          <ol>
            {files.map((file, index) => (
              <li key={index}>{`${file.name}`}</li>
            ))}
          </ol>
          <button className="c-btn_cta" onClick={handleDownload}>
            zip
          </button>
        </div>
      )}
    </>
  );
};
